.company-card {
  width: 240px;
  height: 168px;
  // background: #ffffff 0% 0% no-repeat padding-box;
  box-shadow: 0px 3px 6px #2c28281c;
  border-radius: 10px;
  opacity: 1;
  padding: 10px;

  .content {
    height: 88px;
    img {
      width: 60px;
      height: 64px;
    }
    .handCursor {
      cursor: pointer;
    }
  }

  .label {
    color: #4d4f5c;
    opacity: 1;
    font-size: 18px;
  }
  .email {
    color: #4d4f5c;
    opacity: 1;
    font-size: 13px;
  }
  .date {
    font-size: 12px;
    color: #c4c4c4;
    opacity: 1;
  }
}

.active{
background-color: #ffffff;
}
.suspend{
background-color: #f1f3f9;
}
