.size{
    height: 100vh;
}
.card{
    height: 85%;
    width: 94%;
    max-width: 94%;
    max-height: 85%;
    overflow: hidden;
    margin: auto;
    border-radius: 15px;
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
}
.container .introduction{
    padding-top: 82px !important;
    padding-right: 150px !important;
    padding-left: 150px !important;
}
.introduction .heading{
    font-size:40px;
}
.introduction .desc{
   color: #A9A9A9;
   font-size: 14px;
   line-height: 1.5;
   font-weight: 500;
}
.introduction .login-btn{
    color: #fff;
    padding: 10px 35px !important;
    border-radius: 7px;
    font-weight: 600;
}

.introduction .copyright{
    position: absolute;
    bottom: 10px;
    color:#A9A9A9;

}
.introduction .logo img{
    margin-left: 0;
}
.banner-img{
    height: 85%;
    max-width: 100%;
    display: block;
    margin: auto;
    margin-right: 0;
}