@import url(https://fonts.googleapis.com/css2?family=Poppins&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue','Poppins'
    sans-serif;
    /* font-family: 'Poppins'; */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: Poppins,source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.login-container{
       flex: 1 1;
    display: flex;
    background-color: red;
     }

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.activeMenu{
  background-color: #f6cee6;
}

.searchBox{background:#f7fafc 0% 0% no-repeat padding-box;box-shadow:0px 3px 6px #1717172d;border-radius:25px;opacity:1;width:247px;height:50px;align-items:center}

.company-card{width:240px;height:168px;box-shadow:0px 3px 6px #2c28281c;border-radius:10px;opacity:1;padding:10px}.company-card .content{height:88px}.company-card .content img{width:60px;height:64px}.company-card .content .handCursor{cursor:pointer}.company-card .label{color:#4d4f5c;opacity:1;font-size:18px}.company-card .email{color:#4d4f5c;opacity:1;font-size:13px}.company-card .date{font-size:12px;color:#c4c4c4;opacity:1}.active{background-color:#ffffff}.suspend{background-color:#f1f3f9}

.product-card{width:330px;height:148px;background:#ffffff 0% 0% no-repeat padding-box;box-shadow:0px 3px 6px #2c28281c;border-radius:10px;opacity:1;padding:10px}.product-card .content{height:148px}.product-card .content img{top:192px;left:803px;width:103px;height:20px}.product-card .content .handCursor{cursor:pointer}.product-card .preview{width:20px;height:25px;background:transparent 0% 0% no-repeat padding-box;border:1px solid #11CDEF;border-radius:4px;font-size:10px;text-align:center;color:#11CDEF;opacity:1}.product-card .morevert{color:#c4c4c4}.product-card .label{color:#4d4f5c;opacity:1;font-size:18px}.product-card .category-name{color:#4d4f5c;opacity:1;font-size:10px}.product-card .category{font-size:10px;color:#c4c4c4}.product-card .author{font-size:10px;color:#c4c4c4;opacity:1}.product-card .user-image{width:50}.product-card .product-name{font-size:15px}

.user-card{width:240px;height:100px;background:#ffffff 0% 0% no-repeat padding-box;box-shadow:0px 3px 6px #2c28281c;border-radius:10px;opacity:1;padding:10px}.user-card .content{padding:15px 0px}.user-card .content img{width:50px;height:50px;border-radius:50%;margin-left:10px}.user-card .content .info{margin-left:8px;margin-top:4px;word-wrap:break-word}.user-card .content .handCursor{cursor:pointer}.user-card .label{color:#172B4D;opacity:1;font-size:13px;font-weight:600}.user-card .email{color:#c4c4c4;opacity:1;font-size:14px}.user-card .date{font-size:12px;color:#c4c4c4;opacity:1}


.size{
    height: 100vh;
}
.card{
    height: 85%;
    width: 94%;
    max-width: 94%;
    max-height: 85%;
    overflow: hidden;
    margin: auto;
    border-radius: 15px;
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
}
.container .introduction{
    padding-top: 82px !important;
    padding-right: 150px !important;
    padding-left: 150px !important;
}
.introduction .heading{
    font-size:40px;
}
.introduction .desc{
   color: #A9A9A9;
   font-size: 14px;
   line-height: 1.5;
   font-weight: 500;
}
.introduction .login-btn{
    color: #fff;
    padding: 10px 35px !important;
    border-radius: 7px;
    font-weight: 600;
}

.introduction .copyright{
    position: absolute;
    bottom: 10px;
    color:#A9A9A9;

}
.introduction .logo img{
    margin-left: 0;
}
.banner-img{
    height: 85%;
    max-width: 100%;
    display: block;
    margin: auto;
    margin-right: 0;
}
