.user-card {
    width: 240px;
    height: 100px;
    background: #ffffff 0% 0% no-repeat padding-box;
    box-shadow: 0px 3px 6px #2c28281c;
    border-radius: 10px;
    opacity: 1;
    padding: 10px;
  
    .content {
      padding: 15px 0px;
      img {
        width: 50px;
        height: 50px;
        border-radius: 50%;
        margin-left: 10px;

      }
      .info{
        margin-left: 8px;
        margin-top: 4px;
        word-wrap: break-word;

      }
      .handCursor {
        cursor: pointer;
      }
    }
  
    .label {
      color: #172B4D;
      opacity: 1;
      font-size: 13px;
      font-weight: 600;
    }
    .email {
      color: #c4c4c4;
      opacity: 1;
      font-size: 14px;
    }
    .date {
      font-size: 12px;
      color: #c4c4c4;
      opacity: 1;
    }
  }
  